<template>
    <div>
            账号：<input type="text" v-model="user"> <br>
            密码：<input type="text" v-model="password"> <br>
            <button @click="login">登录</button>

    </div>
</template>
        
<script>
import axios from 'axios'
    export default {
        data() {
            return {
                user:'',
                password:''
            }
        },
        methods: {
            login() {
                axios.post('http://192.168.0.103:5000/login','user='+this.user+'&password='+this.password).then(
                    data=>{
                        if(data.data.msg == '登录成功'){
                            console.log(data.data)
                            alert('登录成功')
                            //触发vuex仓库里的函数,修改登录状态
                            this.$store.commit('changeIsLogin')
                            //把token存到本地
                            localStorage.setItem('vuex_login_token',data.data.token)
                            //登录成功后返回上一层页面
                            this.$router.back();
                        }
                        else{
                            alert('登录失败')
                        }
                    }
                )
            }
        },
    }
</script>

<style  scoped>
   button{
      margin-top: 20px;
      margin-left: 30px;
   }
</style>


